export default {
  middleware: "auth",
  auth: "guest",
  layout: "none",

  data() {
    return {
      userName: "",
      email: "",
      password: "",
      nameError: "Username must be between 6 to 12 characters.",
      emailError: "Enter an Email address that is in valid format.",
      emailAvail: "",
      passError: {
        head: "Password must contain",
        lower: "at lease 1 lowercase letter",
        upper: "at least 1 uppercase letter",
        digit: "at least 1 numerical digit",
        special: "at least 1 special character",
        length: "at least 6 characters long.",
      },
      eAvail: false,
      nError: false,
      eError: false,
      pError: false,
    };
  },

  methods: {
    async onSignUp() {
      try {
        let data = {
          userName: this.userName,
          email: this.email,
          password: this.password,
        };
        let response = await this.$axios.$post(
          `${process.env.DEV_BACKEND}/api/auth/email`,
          data
        );
        if (!response.success) {
          this.eAvail = true;
          this.emailAvail = response.message;
          return;
        } else {
          this.eAvail = false;
          this.emailAvail = "";
        }
        let nSuccess = false;
        let eSuccess = false;
        let pSuccess = false;
        if (!this.checkUserName(data.userName)) {
          this.nError = true;
        } else {
          this.nError = false;
          nSuccess = true;
        }
        if (!this.checkEmail(data.email)) {
          this.eError = true;
        } else {
          this.eError = false;
          eSuccess = true;
        }
        if (!this.checkPassword(data.password)) {
          this.pError = true;
        } else {
          this.pError = false;
          pSuccess = true;
        }

        if (nSuccess && eSuccess && pSuccess) {
          let response = await this.$axios.$post(
            `${process.env.DEV_BACKEND}/api/auth/signup`,
            data
          );

          if (response.success) {
            this.$router.push("/");
          }
        }
      } catch (error) {
        this.email = ""
        this.password = ""
        this.userName = ''
        this.nError = true
        this.pError = true;
        this.eError = true;
        console.log(error);
      }
    },
    checkUserName(data) {
      return data.length >= 6 && data.length <= 12;
    },
    checkEmail(data) {
      return data.includes("@" && ".com");
    },
    checkPassword(data) {
      let letter = /[a-zA-z]+/;
      let upper = /[A-Z]+/;
      let number = /[0-9]+/;
      let special = /[-+_!@#$%^&*., ?]+/;
      let str = letter.test(data) && number.test(data) && special.test(data);
      return data.length >= 6 && str;
    },
  },
};
